/***************
* HEADER
***************/

#wrapper {
  position: relative;
  width: auto;
  min-height: 100%;
  background: none;
  min-width: 980px;
}

#header-wrapper {
  background-color: #006a4d;
}
#wrapper .outer {
  height: 94px;
  max-width: 978px;
  min-width: 320px;
  background-color: #006a4d;
  padding: 0 27px 0 22px;
  margin: 0 auto;
  text-align: left;
}

#header p#logo {
  position: absolute;
  left: 5px;
  top: 24px;
  margin: 0 0 0 3px;
  width: 210px;
  height: 52px;
  z-index: 10;
  overflow: inherit;
  background: transparent url(../assets/logo_header.png) no-repeat scroll 0 0;
}

#header div.loggedIn {
  float: right;
  padding: 34px 0;
  color: #fff;
  font-size: 16px;
  text-align: right;
  margin: 0 24px;
}

#header div.loggedIn ul,
#header div.loggedOut ul {
  margin: 0 0 0 0;
  padding: 0;
  list-style-type: none;
}

#header div.loggedIn ul li,
#header div.loggedOut ul li .cookie {
  display: inline;
  margin-left: 10px;
}

#header div.loggedIn a {
  line-height: 24px;
  font-size: 16px;
  font-weight: normal;
  text-decoration: underline;
  /* font-family:'Arial, Verdana, Helvetica, sans-serif'; */
}

#header .secureMsg {
  float: right;
  margin: 14px 16px 14px 15px;
  padding: 6px 2px 0px 2px;
  width: 280px;
  background: transparent;
  border: 1px solid #fff;
}

#header .secureMsg p a {
  display: block;
  margin: 0;
  padding: 0 0 0 35px;
  text-decoration: underline;
  color: #fff;
  background: none;
}

.secureMsg p:last-child {
  margin-top: 4px;
  margin-left: 4px;
  font-family: "Lloyds_Jack_Light", Arial, Helvetica, sans-serif;
  font-size: 13px;
}

.pageWrap {
  background: none;
  display: block;
  max-width: 978px;
  min-width: 320px;
  margin: 0 auto;
  padding: 20px;
  text-align: left;
}

#page {
  padding: 12px 12px 12px 0px;
  background: none;
}

.primary {
  position: relative;
  float: left;
  width: 100%;
  margin: 12px 6px;
  padding: 0;
  background-color: #fff;
  border: none;
  z-index: 1;
}

/***************
*   FOOTER
****************/

#footer {
  background-color: #006a4d;
  bottom: 0;
  left: 0;
  width: 100%;
}

#footerInner {
  position: relative;
  margin: 0 auto !important;
  padding: 12px 0;
  border-bottom: 0;
  text-align: left;
  max-width: 980px !important;
}

#footerInner p {
