/***************
*   CHECKBOX
****************/

.loginCheckbox {
  display: block;
  position: relative;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  padding-left: 35px;
}
.loginCheckbox input {
  -webkit-appearance: none;
  -moz-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.loginCheckbox input {
  position: absolute;
  top: 0;
  left: 0;
  height: 32px;
  width: 32px;
  background-color: #fff;
  border: 1px solid #767676;
  margin-left: 0px;
}

.loginCheckbox input:checked {
  background-color: #72a418;
}

.loginCheckbox input:after {
  content: "";
  position: absolute;
  display: none;
}

.loginCheckbox input:checked:after {
  display: block;
}

.loginCheckbox input:checked:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

/***************
*   TOOLTIP
****************/
.cxtHelp {
  display: inline-block;
  width: 15px;
  height: 15px;
  background: #fff;
  border: 2px solid #006a4d;
  border-radius: 250px;
  text-align: center;
  padding: 1px;
  margin-top: 6px;
  margin-left: 4px;
}
a.cxtTrigger {
  font-size: 13px;
  color: #006a4d;
  font-weight: bolder;
  text-decoration: none;
}

#cxtHelp1 {
  padding: 10px;
  background: #ecf0f2;
  border: 1px solid #acacac;
}

#cxtHelp1 h3 {
  font-size: 12px !important;
}

.content .cxtHelp {
  display: inline-block; /* For Safari */
  position: relative;
}
.content .cxtHelp .refPoint .contextual .help h3 {
  /*galaxy 23845 - prevents indentation of tooltips within the accounts wrapper*/
  margin-left: 0;
  padding-left: 0;
}
.content .fieldHelp .cxtHelp,
.content .radioFieldPanel .cxtHelp {
  font-size: 12px;
}
.content h2 .cxtHelp {
  font-size: 0.8em;
}
.content .fieldHelp .cxtHelp,
.content h2 .cxtHelp {
  z-index: 999 !important;
}
/* fix for GALAXY-8546 */
.content .panel .inner .inlinerow .cxtHelp {
  font-size: 12px !important;
}

.content .fieldHelp .addHelp {
  display: block;
  clear: left;
  margin-bottom: 0.7em;
}

.content table thead th .cxtHelp {
  margin-right: -15px;
}

.content .cxtHelp a.cxtTrigger {
  display: inherit !important; /* fix ff2 */
  font-size: 1em !important;
  font-weight: bold;
  text-decoration: none;
}
.content .cxtHelp a:hover {
  text-decoration: underline;
}

/*making tooltips inline with checkboxes/radios consistent*/
.content .formFieldInner span.label span.cxtHelp,
.content .formFieldInner label span.cxtHelp {
  margin-left: 3px;
  font-size: 1em !important;
}

.content .checkbox .formFieldInner .cxtHelp a.cxtTrigger {
  font-size: 13px !important;
  margin-top: 0px;
}
.content .formFieldInner label span.cxtHelp,
.content .formFieldInner label .cxtHelp a.cxtTrigger {
  margin-left: 0 !important;
}

.content .cxtHelp .refPoint {
  position: absolute;
  left: 0;
  top: 0;
}
.content .cxtHelp .contextual {
  position: absolute;
  left: -22px;
  bottom: 8px;
  padding: 0;
  width: 320px;
  z-index: 99999;
  /* Standard */
  border-radius: 8px;
  /* Mozilla */
  -moz-border-radius: 8px;
  /* Webkit */
  -webkit-border-radius: 8px;
  background: #d3cfcf;
  text-align: left;
}

.content .cxtHelp .contextual ul.linkList li {
  text-align: left;
}

.content .cxtHelp .contextual.left {
  left: -282px;
}
.content .cxtHelp .contextual .pointer {
  position: absolute;
  left: 20px;
  bottom: -11px;
  width: 19px;
  height: 13px;
  background: url(../assets/cxthelp_arrow.png) 0 0 no-repeat;
}
.content .cxtHelp .contextual.left .pointer {
  left: 280px;
}
.content .cxtHelp .help {
  /* fix for ie */
  /* this line was originally written with two slashes at the front //position: relative; */
  /* unsure as to the orginal intent of the // */
  position: relative;
  left: 0;
  top: -1px;
  margin: 0;
}
.content .cxtHelp .help h2 {
  margin-right: 20px;
  font-size: 13px;
  color: #323233;
}
.content .dateGroup .cxtHelp .help h2 {
  font-size: 12px;
}

.content .cxtHelp .help ul.linkList {
  float: none !important;
}

.content .cxtHelp .help p {
  display: block !important;
  float: none !important;
  margin: 0 0 1em !important;
  padding: 0 !important;
  font-size: 13px !important;
  font-weight: normal !important;
  color: #323233;
  background: none !important;
}

.content .cxtHelp p.close,
#overlay .cxtHelp p.close {
  position: absolute;
  right: 9px;
  top: 9px;
  width: 14px !important;
  height: 14px;
  overflow: hidden;
  margin: 0 !important;
  padding: 1px !important;
  background: url(../assets/icons/cross.png) 0 0 no-repeat !important;
}
.content .cxtHelp p.close a {
  display: block;
  width: 14px !important;
  height: 13px;
  overflow: hidden;
  margin: 0 !important;
  padding: 0 !important;
  padding-top: 16px !important;
  background: transparent !important;
}

.content label .cxtHelp a.cxtTrigger {
