body {
  min-width: 651px;
  font-family: Arial, Verdana, Helvetica, sans-serif;
  font-size: 62.5%;
  text-align: center;
  color: #323233;
  background: #fff;
  word-wrap: break-word;
  line-height: 1.3;
}

@font-face {
  font-family: "Lloyds_Jack_Medium";
  src: url(./assets/fonts/lloyds_bank_jack-mediumWEB.woff) format("woff");
}

@font-face {
  font-family: "Lloyds_Jack_Light";
  src: url(./assets/fonts/lloyds_bank_jack-lightWEB.woff) format("woff");
}
