/*
-----------------------------------------------
Galaxy IB - SCA Login CSS: Base
----------------------------------------------- */

/* Suggested rule structure
 * 
 * display/visibility:
 * position:
 * z-index:
 * top:
 * right:
 * bottom:
 * left:
 * float:
 * clear:
 * width:
 * height:
 * margin:
 * padding:
 * overflow:
 * list-style:
 * font..:
 * line-height:
 * text-align:
 * colour:	
 * border:
 * background:
----------------------------------------------- */

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
}
body {
  min-width: 651px; /* For Gecko */
  font-family: Arial, Verdana, Helvetica, sans-serif;
  font-size: 62.5%;
  text-align: center; /*Centre content for IE5 */
  color: #323233;
  background: #fff;
  word-wrap: break-word;
}

p {
  font-family: "Lloyds_Jack_Light", Arial, Helvetica, sans-serif;
  font-size: 14px;
}
/* Special formatting (inc. mini Reset)
---------------------------------------*/
body {
  line-height: 1.3;
}
a img {
  border: 0;
}
fieldset,
form,
legend {
  margin: 0;
  padding: 0;
  border: 0;
}

/* Mobile - prevent mobile browsers adjuting font sizes
---------------------------------------*/
@media screen and (max-device-width: 480px) {
  /* Prevent iOS, WinMobile from adjusting font size */
  html {
    -webkit-text-size-adjust: none;
    -ms-text-size-adjust: none;
  }
}

/* Header
---------------------------------------*/
#header {
  position: relative;
  min-height: 90px; /* fix for ie6 */
  margin: 0;
  padding: 0 0 0 390px;
  border-top: 0;
}

/*begin logo check*/
#header p#logo span img {
  display: block;
  visibility: hidden;
}
/*end logo check*/
/* logged in */
#header div.loggedIn {
  float: right;
  padding: 11px 0 0 0;
  color: #fff;
  font-size: 1.2em;
  text-align: right;
}
#header div.loggedIn a,
#header div.loggedOut a,
#header ul.loggedHeaderLinks li a {
  color: #fff;
  font-weight: bold;
  text-decoration: none;
}
#header div.loggedIn a:hover,
#header div.loggedOut a:hover,
#header ul.loggedHeaderLinks li a:hover {
  text-decoration: underline;
}

/* amended for GALAXY-9095 - the link for the cookie is coming from the CMS where only 1 CSS class name can be assigned (anchor can have the newwin class) */
#header ul.loggedHeaderLinks li a.linkBullet,
#header ul.loggedHeaderLinks li.cookie a {
  padding-left: 16px;
  background: url(../assets/icons/arrow_lo.png) 0 0 no-repeat;
  font-size: 1.2em;
}

/* logged in links */
#header div.loggedIn ul,
#header div.loggedOut ul {
  margin: 0 0 1.2em 0;
  padding: 0;
  list-style-type: none;
}
#header div.loggedIn ul li,
#header div.loggedOut ul li {
  display: inline;
  margin-left: 10px;
}
#header div.loggedIn ul li.viewOnly {
  /* fix for ie6 */
  padding-left: 19px;
  background: transparent url(../assets/icons/view_only.png) 0 1px no-repeat;
}
#header div.loggedIn ul li.secure {
  /* fix for ie6 */
  padding-left: 19px;
  background: url(../assets/icons/padlock_header.png) 0 -1px no-repeat;
}

#header ul.loggedHeaderLinks {
  float: right;
  list-style-type: none;
  margin-right: 10px;
}

#header ul.loggedHeaderLinks li {
  display: inline;
  margin-left: 10px;
}

/* logged in user and logout button */
#header div.loggedIn p.user {
  margin: 0;
}
#header div.loggedIn p.user span.name {
  font-size: 1.7em;
  font-weight: bold;
  display: inline-block;
  padding-top: 2px;
  vertical-align: top;
}
#header div.loggedIn p.user a.logout {
  /* fix for ie6 and ie7 */
  margin: 0 0px 0 8px;
  display: inline-block;
  width: 75px;
  height: 25px;
  padding-top: 2px;
}

/* logged out */
#header div.loggedOut {
  /* fix for ie6 */
  min-height: 77px;
  min-width: 307px;
  float: right;
  padding: 13px 0 0;
  color: #fff;
  font-size: 1.2em;
  text-align: right;
}
#header div.loggedOut p.msg {
  margin: 0;
  font-size: 1.2em;
  font-weight: bold;
}
#header div.loggedOut p.login {
  margin: 1.2em 0 0 -2px;
}

#header div.loggedOut ul li.informational {
  padding-left: 12px;
  border-left: 1px solid #fff;
}

#header .secureMsg p.msg {
  min-height: 16px;
  margin: 0;
  padding: 5px 0 0 35px;
  font-size: 1.6em;
  background: url(../assets/padlock.png) 4% 0 no-repeat;
  background-size: 13px;
}

